import { VisualId } from '@tableau/api-internal-contract-js';
import { ApiService } from './ServiceRegistry';

/**
 * Service for implementing the logic for calls to support accessibility
 *
 * @interface AccessibilityService
 * @extends {ApiService}
 */
export interface AccessibilityService extends ApiService {
  /**
   * @param visualId
   * @param tupleId
   */
  getTooltipTextAsync(visualId: VisualId, tupleId: number): Promise<String>;

  /**
   * @param visualId
   */
  leaveMarkNavigationAsync(visualId: VisualId): Promise<void>;
}
